<template lang="pug">
    div(id="app")
        go-top(:size="40" :bottom="15" :right="15" bg-color="#112761")
        div(class="main-wrapper")
            section.loading(v-if="!isLoaded")
                img(src="@/assets/images/loading.gif", class="img-fluid", alt="")
            transition(name="drain")
                router-view
</template>

<script>
import Vue from "vue";
import GoTop from "@inotom/vue-go-top";
Vue.use(GoTop);

export default {
    name: "App",
    components: {
        GoTop,
    },
    data: () => {
        return {
            isLoaded: false,
        };
    },
    mounted() {
        document.onreadystatechange = () => {
            if (document.readyState == "complete") {
                this.isLoaded = true;
            }
        };
    },
};
</script>

<style lang="scss">
.vue-go-top {
    &:hover,
    &:focus {
        outline: none;
    }
}
.slither-enter-active,
.slither-leave-active {
    transition: transform ease 0.7s;
}

.slither-enter,
.slither-leave-to {
    transform: translateX(100%);
}

.slither-enter-to,
.slither-leave {
    transform: translateX(0);
}

.drain-enter-active,
.drain-leave-active {
    transition: transform ease 1s;
}

.drain-enter,
.drain-leave-to {
    transform: translateY(100%);
}

.drain-enter-to,
.drain-leave {
    transform: translateY(0);
}
</style>
